    #blog-post section.hero__fw,
    #client-post section.hero__fw {
    overflow: hidden;
    padding:0;
    }

    .hero {
        display: flex;
        align-items: center;
        justify-content:  center;
        width: 100vw;
        height: 90vh;
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        @include mq (mid){
            height: 70vh;
        }
            @include mq (small){
                height: 50vh;
            }
        }
    
    #blog-post .hero {
      background-image: url("../img/katherine.png");
    }

    #client-post .hero {
        background-image: url("../img/Foto_Hadisa2.png");
      }
      
  .post__body {
    display: grid;
    grid-gap: 48px;
    grid-template-columns: 0.6fr auto 1fr;
    margin-left: auto;
    margin-right: auto;
    padding: 25px 75px 75px 75px ;
    @include font-size($p-header-font-sizes);
    .post__main {
        max-width: 750px;
        @include mq(mid) {
            max-width: 506px;
        }
    }
  }

  .post__sidenav {
    margin-left:auto;
    margin-right:auto;
    .sidenav__bg{
    background: var(--clr-umber-100);
    border-radius: 10px;
    padding: 20px;
    padding-left: 35px;
    }
          h5 {
            font-family: $sans;
            margin-bottom: 15px;
            margin-left: -15px;
          }
          ol {
            @include flexCenter(column);
            align-items: flex-start;
            color: var(--clr-green-300);
            font-family: $sans;
            list-style-type: decimal;
            max-width:230px;
            li {
              margin-top: 5px;
              padding:5px;
              line-height: 1.2;
              a {
                color: var(--clr-green-300);
                transition: $default-transition;
                &:hover{
                color: var(--clr-green-500);
                }
              }
            }
          }
  }
  
  .post__nav{
      margin-left:auto;
      margin-right:auto;
      a{
        @include hover-line-revers;
        &::after {
        content:url(../img/Vector.svg);
        transform: rotate(180deg);
        left: -15px;
        top:10px;
        border-bottom:0;
        position: absolute;
        }

      }
  }


  .post__header {
    grid-column: 2 / 3;
    font-family: $sans;
    .post__tags{
      .tag {
        background: #F2E2D7;
        border-radius: 2px;
        padding: 2px 5px;
        font-family: $sans;
        color: var(--clr-umber-300);
      }
    }
    h2 {
      @include font-size($h1-font-sizes);
      margin-top: 30px;
    }
    .post__date {
      margin-bottom: 30px;
      color: var(--clr-green-300);
      font-family: $sans;
    }
  }
