  #client-post .aside__background {
    background-color: var(--clr-green-100);
    padding: 1.5em;
    border-radius: 10%;
  }
  
  #blog-post .blog-post__header,
  #client-post .client-post__header {
    padding: 75px;
    margin-top: 75px;
    display: grid;
    grid-template: auto 1fr auto;
    margin-right: auto;
    margin-left:auto;
    .post__nav{
      position:fixed;
        grid-column: 1 / 2;
        a     {
          position: relative;
          margin-right:auto;
          @include hover-line-revers;
              &::after {
              content:url(../img/Vector.svg);
              margin-left:10px;
              top:10px;
              border-bottom:0;
              position: absolute;
              }
        } 
      }
    

      section {
        padding: 0 48px;
        p {
          margin-bottom: 25px;
        }
    }
      .post__aside {
        grid-column: 3 / 4;
        max-width: 234px;
        padding: 25px;
        .aside__background {
          h5 {
            font-family: $sans;
            margin-bottom: 15px;
          }
          ol {
            @include flexCenter(column);
            align-items: flex-start;
            color: var(--clr-green-300);
            font-family: $sans;
            li {
              line-height: 1.5;
            }
          }
        }
  
  
      }
    }
  
  .post__subheading {
      font-family: $sans;
      @include font-size($h3-font-sizes);
      margin: 20px 0 10px 0;
    }
  
